@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@200&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@200&display=swap);
* {
  background-color: #212529;
}

.card-div {
  min-width: 300px;
  color: white;
  position: fixed;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

.account-redirect {
  color: #fff;
  text-align: center;
}

.forgot-password {
  text-align: center;
  padding: 1rem;
}

.text-center {
  padding: 2rem;
}

.movie {
  background-color: #0275d8;
  border-top-right-radius: 3px;
  border-top-right-radius: 3px;
  overflow: hidden;
  position: relative;
  box-shadow: 3px 3px 5px rgba(0, 0, 0, 0.1);
  margin: 1rem;
  width: 250px;
}

.movie img {
  max-width: 100%;
}

.movie-info {
  background-color: #0275d8;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 1rem;
}

.movie-info h3 {
  background-color: #0275d8;
  color: white;
  margin: 0;
}

.movie-over {
  background-color: #fff;
  position: absolute;
  color: #0275d8;
  text-align: center;
  -webkit-transform: translateY(100%);
          transform: translateY(100%);
  padding: 1rem;
  bottom: 0;
  left: 0;
  max-height: 100%;
  border-radius: 3px;
  overflow: hidden;
  right: 0;
  transition: -webkit-transform 0.3s ease-in-out;
  transition: transform 0.3s ease-in-out;
  transition: transform 0.3s ease-in-out, -webkit-transform 0.3s ease-in-out;
}

.movie-over h2 {
  background-color: #fff;
  color: #0275d8;
}

.movie-over p {
  background-color: #fff;
  color: black;
}

.movie:hover .movie-over {
  color: white;
  background-color: white;
  -webkit-transform: translateY(0%);
          transform: translateY(0%);
}

span {
  background-color: rgba(0, 0, 0, 0.87);
  border: none;
  padding: 2px;
  border-radius: 2px;
  color: yellow;
}

* {
  box-sizing: border-box;
}
body {
  background-color: #212529;
  color: white;
  font-family: "Poppins", sans-serif;
  margin: 0;
}

header {
  width: 100%;
  padding: 1rem;
  background-color: #21252975;
  display: flex;
  justify-content: space-between;
  border-bottom: 1px solid #f8f9fa;
}
.header-wrapper {
  margin: 0 auto;
  border-bottom: #f8f9fa;
  width: 90%;
  padding: 1rem;
  background-color: #212529;
  display: flex;
  justify-content: space-between;
}
.movie-container {
  background-color: #212529;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  width: 100%;
}

.search {
  background-color: transparent;
  border: 2px solid #0275d8;
  color: white;
  text-align: center;
  padding: 0.5rem 3.5rem;
  outline: hidden;
  border-radius: 50px;
  font-family: inherit;
  font-size: 1.2rem;
}

.search:focus {
  outline: 0;
}

.welcome-div {
  display: flex;
  justify-content: space-between;
  width: 100%;
  text-align: left;
  padding-bottom: 2rem;
  padding-top: 2rem;
}

.terms {
  color: white;
  text-align: center;
  width: 30%;
  padding: 1rem;
}

@media only screen and (max-width: 600px) {
  header {
    float: none;
    display: block;
    text-align: center;
    justify-content: center;
    border-bottom: #f8f9fa;
  }
  .header-wrapper {
    border-bottom: #f8f9fa;
    margin: 0 auto;
    float: none;
    display: block;
    text-align: left;
    text-align: center;
    justify-content: center;
  }
  .search {
    padding: 0.5rem 1rem;
    margin-top: 2rem;
    font-size: 1rem;
  }

  .terms {
    color: white;
    text-align: center;
    width: 100%;
    margin: 0 auto;
  }
  .welcome-div {
    padding-top: 0;
    padding-bottom: 0;
    width: 90%;
    margin: 0 auto;
    flex-wrap: wrap;
    text-align: center;
  }
}

